.contact-body{
    width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contact-container{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 20px 100px;
}
.contact-container::before{
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: url('../assets/img/63568-mobile-banking.gif') no-repeat center;
	background-size: cover;
	z-index: -1;
	filter: blur(50px);
}
.contact-box{
	max-width: 850px;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	justify-content: center;
	align-items: center;
	text-align: center;
	background: #fff;
	box-shadow: 0 0 19px 5px rgba(0, 0, 0, 0.19);
}
.left{
	height: 100%;
	background: url('../assets/img/63568-mobile-banking.gif') no-repeat center;
	background-size: cover;
}
.right{
	padding: 25px 40px;
}
.right h2{
	position: relative;
	padding-bottom: 10px;
	margin-bottom: 10px;
}
.right h2::after{
	content: '';
	position: absolute;
	left: 50%;
	bottom: 0;
	transform: translateX(-50%);
	height: 4px;
	width: 50px;
	border-radius: 2px;
	background: #2ecc71;
}
.contact-box .right .field-input{
	width: 100%;
	padding: 0.5em 1em;
	outline: none;
	border: 2px solid rgba(0, 0, 0, 0.0);
	font-size: 1.1em;
	margin-bottom: 22px;
	transition: .3s;
	background: rgba(230,230,230,.6);
}
.contact-box .right .field-input:hover{
	background: rgba(0, 0, 0, 0.1);
}
.contact-box .right .field-input:focus{
	background: #fff;
	border: 2px solid rgba(30,85,250,.47);
}
.area{
	min-height: 150px;
}
.contact-box .btn{
	width: 100%;
	padding: 0.5em 1em;
	font-size: 1.1em;
	background: #2ecc71;
	cursor: pointer;
	outline: none;
	border: none;
	transition: .3s;
	color: #fff;
}
.contact-box .btn:hover{
	background: #27ae60;
}
@media screen and (max-width: 880px){
	.contact-box{
		grid-template-columns: 1fr;
	}
	.left{
		height: 200px;
	}
}