.error-container{
	width:100%;
	overflow: hidden;
}
.error{
	min-height: 100vh;
	background: linear-gradient(0deg, #fff,#03a9f4);
}
.sky{
	position: relative;
	width: 100%;
	height: 60vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.sky h2{
	font-size: 16em;
	color: #fff;
	text-shadow: 15px 15px 0 rgba(0, 0, 0, 0.1);
}
.sky h2 span{
	display: inline-block;
	animation: animate1 2s ease-in-out infinite;
}
.sky h2 span:nth-child(even){
	animation-delay: -1s;
}
@keyframes animate1{
	0%,100%{
		transform: translateY(-50px);
	}
	50%{
		transform: translateY(50px);
	}
}
.field{
	padding: 100px;
	height: 40vh;
	background: #6e2308;
	box-shadow: inset 0 20px 10px #51680c;
	text-align: center;
}
.field h2{
	color: #fff;
	font-size: 2em;
	margin-bottom: 20px;
}
.field a{
	background: #fff;
	text-decoration: none;
	color: #222;
	width: 160px;
	height: 50px;
	line-height: 50px;
	border-radius: 50px;
	display: inline-block;
	font-size: 20px;
}
.plane{
	position: absolute;
	bottom: 100px;
	left: 100px;
	max-width: 300px;
}
.grass{
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 20px;
	background: url(../assets/img/grass.png);
	background-position: bottom;
	animation: animateGrass 0.2s linear infinite;
}
@keyframes animateGrass{
	0%{
		background-position: 0 0;
	}
	100%{
		background-position: 94px 0;
	}
}